import React, { useRef, useEffect, useCallback, useState, useMemo } from 'react'

// import useWindowDimensions from '../../hooks/useWindowDimensions'
import { Canvas } from './RuneCanvas.style'

interface RuneCanvasProps {
  startAnimation: boolean
}
const RuneCanvas: React.FC<RuneCanvasProps> = (props: RuneCanvasProps) => {
  const canvasRef = useRef<HTMLCanvasElement | null>(null)
  const canvasCtxRef = React.useRef<CanvasRenderingContext2D | null>(null)
  // const { height, width } = useWindowDimensions()
  const [chance, setChance] = useState(0)
  const { startAnimation } = props
  interface Sprite {
    radius: number
    position: number
    width: number
  }
  const offSets: Sprite[] = useMemo(() => {
    const arr = []
    for (let j = 0; j < 40; j++) {
      arr.push({
        radius: Math.random() * 150,
        position: Math.random() * 200,
        width: Math.random() * 20,
      })
    }
    return arr
  }, [])

  const drawLogo = useCallback(
    (ctx: CanvasRenderingContext2D) => {
      // Initialize
      if (canvasRef.current) {
        // ctx!.scale(0.5, 0.5)
        const scale = 0.5
        ctx.fillStyle = '#00000019'
        ctx.fillRect(0, 0, 1000 * scale, 1000 * scale)
        ctx.beginPath()
        ctx.lineWidth = 35 * scale
        // const opacity = 0xaa - 10
        /* eslint-disable prefer-template */
        const gradient = ctx.createLinearGradient(
          400 * scale,
          500 * scale,
          1000 * scale,
          500 * scale,
        )
        gradient.addColorStop(0, '#00ccff')
        gradient.addColorStop(0.5, '#33ff99')
        gradient.addColorStop(1.0, 'white')
        ctx.strokeStyle = gradient // opacity.toString(16)
        ctx.arc(500 * scale, 500 * scale, 250 * scale, 0, Math.PI * 2)
        ctx.stroke()
        ctx.beginPath()
        // onst opacity = 0xaa - 10
        /* eslint-disable prefer-template */
        ctx.lineTo(450 * scale, 386 * scale)
        ctx.lineTo(450 * scale, 600 * scale)
        ctx.moveTo(460 * scale, 402 * scale)
        ctx.lineTo(550 * scale, 450 * scale)
        ctx.lineTo(510 * scale, 500 * scale)
        ctx.lineTo(550 * scale, 590 * scale)
        ctx.lineTo(550 * scale, 600 * scale)
        ctx.stroke()

        // const opacity = 0xaa - 10
        /* eslint-disable prefer-template */
        // const gradient2 = ctx.createLinearGradient(
        //   500 * scale,
        //   500 * scale,
        //   1000 * scale,
        //   500 * scale,
        // )
        // gradient2.addColorStop(0, '#c8e6ee')
        // gradient2.addColorStop(0.5, 'transparent')
        // gradient2.addColorStop(1.0, 'transparent')
        ctx.lineCap = 'round'

        // ctx.strokeStyle = gradient
        // ctx.beginPath()
        // ctx.lineWidth = scale * 10
        // ctx.arc(
        //   500 * scale,
        //   500 * scale,
        //   350 * scale,
        //   (chance * 5) / 75,
        //   (chance * 5) / 75 + Math.PI / 50,
        // )
        // ctx.stroke()

        if (startAnimation) {
          ctx.lineCap = 'butt'
          for (let i = 0; i < offSets.length; i++) {
            if (i % 2 === 0) {
              orbit(
                ctx,
                gradient,
                scale,
                -1 * chance + offSets[i].position,
                (275 + offSets[i].radius) * scale,
                offSets[i].width,
              )
            } else {
              orbit(
                ctx,
                gradient,
                scale,
                chance + offSets[i].position,
                (275 + offSets[i].radius) * scale,
                offSets[i].width,
              )
            }
          }
          // orbit(ctx, gradient, scale, -chance + 10, 390 * scale, 10)
          // orbit(ctx, gradient, scale, chance + 80, 400 * scale, 5)
          // orbit(ctx, gradient, scale, -chance + 300, 450 * scale, 3)

          // orbit(ctx, gradient, scale, -chance + 80, 390 * scale, 8)
          // orbit(ctx, gradient, scale, chance + 100, 400 * scale, 9)
          // orbit(ctx, gradient, scale, -chance + 500, 450 * scale, 8)

          // orbit(ctx, gradient, scale, chance + 11, 300 * scale, 10)
          // orbit(ctx, gradient, scale, -chance + 87, 420 * scale, 5)
          // orbit(ctx, gradient, scale, chance + 330, 410 * scale, 3)

          // orbit(ctx, gradient, scale, chance + 20, 360 * scale, 1)
          // orbit(ctx, gradient, scale, -chance + 160, 340 * scale, 2)
          // orbit(ctx, gradient, scale, chance + 800, 350 * scale, 3)
        }
        ctx.lineCap = 'butt'
      }
    },
    [chance, offSets, startAnimation],
  )

  const orbit = (
    ctx: CanvasRenderingContext2D,
    gradient: CanvasGradient,
    scale: number,
    angle: number,
    radius: number,
    lineWidth: number,
  ) => {
    ctx.lineCap = 'round'

    ctx.strokeStyle = gradient
    ctx.beginPath()
    ctx.lineWidth = (scale * lineWidth) / 3
    ctx.arc(
      500 * scale,
      500 * scale,
      radius,
      angle / 25,
      angle / 25 + Math.PI / 30,
    )
    ctx.stroke()
  }

  const animate = useCallback(() => {
    if (canvasRef.current) {
      canvasCtxRef.current = canvasRef.current.getContext('2d')
      const ctx = canvasCtxRef.current
      // const y = height / 2

      drawLogo(ctx as CanvasRenderingContext2D)

      setTimeout(() => {
        setChance(chance + 1)
      }, 1000 / 30)
      /* eslint-disable prefer-template */
    }
  }, [chance, drawLogo])
  useEffect(() => {
    requestAnimationFrame(animate)
    // Initialize
  }, [animate])
  return <Canvas ref={canvasRef} width={500} height={500} />
}

export default RuneCanvas
